// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import App from './App'
import Axios from './util/myaxios'
import VueRouter from 'vue-router'
import routerMap from './router'
import store from './store/store'
import staticConfig from './util/config'
import util from './assets/js/util'
import axios from 'axios'

import funcext from './util/funcext'
import zepto from 'webpack-zepto'

import axiosUtil from './api'
import pouchdb from 'pouchdb'

import customFilters from './util/filters'
import customDirectives from './util/directive'
import md5 from './util/md5'
import nativeBridge from 'util/nativeBridge'
//import custMixin from  'util/custMixin'
// import loki from './lib/lokijs/src/lokijs.js'
  // require('./lib/lokijs/src/lokijs.js')
  // require('./lib/lokijs/src/loki-indexed-adapter.js')
//util.initRem(document,window);
//console.log(md5(12345))
if(window.browser.versions.mobile){
  nativeBridge.init();
 }
const DEBUG_MODE = true
global.URL_PREFIX = DEBUG_MODE ? ' /api' :'http://192.168.51.199:7001';// ' http://192.168.1.100:7001'; //
Vue.use(VueRouter)
Vue.use(MintUI)
const router = new VueRouter({//添加权限控制
  routes: routerMap
});
router.beforeEach((to,from,next)=>{
  // console.log("to.path===========",to.path);
  // console.log("from.path===========",from.path);
    if(to.matched.some(m=>m.meta.auth)){
      if(!global.DEBUGMODE){
        axiosUtil.isLogin().then(data=>{
            var message=data.data;
            if(message.errorcode==0){
                next();
            }else{
               next({path:"/login",query:{referrer:to.fullPath}});
            }
          })
      }else{
        next();
      }
          
    }
    else if(to.path=="/login"){
          axiosUtil.isLogin().then(data=>{
            var message=data.data;
            if(message.errorcode==0){
              // console.log("已登录不走登录");
               window.router.go(-1);
            }else{
              next();
            }
          })
    }
    else{
      next();
    }
})
Vue.config.productionTip = false
var pp = {
  "App": App
}
Object.keys(customFilters).forEach(key=>{
    Vue.filter(key,customFilters[key]);
})
Object.keys(customDirectives).forEach(key=>{
    Vue.directive(key,customDirectives[key]);
})
global.router = router
//Vue.prototype.$http = Axios;//this.$http.api
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  //mixins:[custMixin],
  created:function(){
  },
  template: '<App/>',
  components: { "App":pp.App }
})



window.Vue = Vue
window.$ = zepto
window.axiosUtil=axiosUtil
window.md5=md5



