<template>
  <transition name="slide-menu">
  <div class="slidemenu" v-show="visible" :value="value.isShow"  @click="hide" >
  	<div class="slidemenu-conent" @click.stop="test">
          <input type="text" v-model="a.str" />
   	
   </div>
  </div>
  </transition>
</template>

<script>

export default {
  name: 'slidemenu',
  data () {
    return {
      isShow2:true,
      visible:true,
      myDb:'',
      col:'',
      menuName:'',
      menuList:[],
      pdname:'',
      pddata:'',
    }
  },
  props:{
      value:{
        type:Object,
        default:function() {
        return {
          isShow:false
        };
        }
      },
      a:{
        type:Object,
        default:function(){
          return {
            str:"a"
          }
        }
      }
    },
  computed:{

  },
  created:function(){
   
  },
  watch:{
        a(v){
          console.log(v);
        },
       value(val) {
            this.visible = val.isShow;
          },
           visible(val) {
            this.value.isShow=true;
          }
    },
  methods:{
    hide:function(){
      this.visible=false;
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.slidemenu{
/*	height: 100%;
	width: 100%;
	position: fixed;
	top: 0;
  left:0;
	background-color: rgba(0,0,0,.3);
	z-index: 998;
  transition: 3s all ease;*/
  position: fixed;
      width: 100%;
      height: 100%;
      z-index: 999;
      left: 0;
      top: 0;
        background-color: rgba(0,0,0,.3); 
    transition: .8s all ease;
}
.slidemenu-conent{
	height: 100%;
	width: 80%;
	background: #fff;
}
.menu-list{
  color: #111;
}

</style>



