import axios from 'axios';
import Qs from 'qs'
import cookieUtil from 'js-cookie'
import nativeBridge from 'util/nativeBridge'
import shopApi from './shopApi.js'

// import areas from 'asssts/json/areas.json'
axios.defaults.timeout = 5000; //响应时间
axios.defaults.withCredentialst = true;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';           //配置
//POST传参序列化
axios.interceptors.request.use(
    config => {
      if(config.method  === 'post' && config.headers.post['Content-Type'] === 'application/x-www-form-urlencoded;charset=UTF-8' && !config.headers['Content-Type']){

          config.data = Qs.stringify(config.data);
      }
      if (cookieUtil.get('csrfToken') && !(/^(GET|HEAD|OPTIONS|TRACE)$/.test(config.method)) ) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
          console.log(cookieUtil.get('csrfToken'))
             config.headers['x-csrf-token'] = cookieUtil.get('csrfToken')
         }
      return config;
    },
    error =>{
    return Promise.reject(error);
});

// http response 拦截器
axios.interceptors.response.use(
    response => {
        //response.setHeader('Access-Control-Allow-Credentials',"127.0.0.1:8040");
        if(response.data && response.data.errorcode && response.data.errorcode==-2){//约定-2未登录
            //跳转到登录页面
             cookieUtil.remove("trdAcct", {path:global.webSite});
         cookieUtil.remove("userCode",{path:global.webSite});
         if(!global.DEBUGMODE){
         // window.router.push('/login');//登录判断放入权限控制里
         }
            
          }

        return response;
    },
    error => {
        if (error.response) {
            switch (error.response.status) {
                case 401:break;//假定401为约定好的后台session失效的状态吗，页面重新跳转到登陆页清掉cookie缓存
                case 500:break;
                case 403 ://未登录
                console.log(error.response.data)
                console.log("参数转化错误")

                //router.push('/login')
            }
                return Promise.reject(error.response.data)   // 返回接口返回的错误信息
        }else{
            console.log(error)
        }

    });
const DEBUG_MODE = global.DEBUGMODE;//
var URL_PREFIX = DEBUG_MODE ? ' /api'+global.webSite :(global.IPPORT+global.webSite)//('http://10.0.37.41:8081'+global.webSite);//'http://10.0.37.45:8082/shop-web';//'http://10.0.37.45:8082';// ' 37.27; //
