

export default {
    dateFormat:function(time){
          return dateFormatter(time);
      },
      bankcard:function(bankNum){
        return "**" + (bankNum+"").substr(-4)
      },
      plusZero:function(num){
        return num + ".00"
      }
      ,
      toArea:function(areaNum,areaId){
          var data;
          window.toArea(areaId,areaNum).then(data => {
            console.log("toArea",data);
            data = data
          })
          return data;
      },
        toMoney:function(value){
             return toMoney(value);
        },
        timetext:function(value){
        	var v = '' + window._trim(value);
					var flag=v.indexOf(":")==-1;
					if(v){
						if (v.length == 5) {
							return ('0' + v.substring(0,1) + ':' + v.substring(1,3) + ':' + v.substring(3,5));
						}
						else if(v.split(".")[0].	length > 6){
							if(flag){return (new Date(parseInt(v)).Format('hh:mm:ss'));
								}else{
									return v;
								}
							}
						else{return (v.substring(0,2) + ':' + v.substring(2,4) + ':' + v.substring(4,6));
						}
					}else {
						return '';
					}
        },
        datetext:function(value){
        	 return window.dateFormatter(value)
        },
        datetimetext:function(v){
        	if (v && v !== 0){
						if((v+'').length == 12){
							v = v + '';
							return (v.substring(0,4) + '-' + v.substring(4,6) + '-' + v.substring(6,8) + ' ' + v.substring(8,10) + ':' + v.substring(10,12)+ ':00');
						}else{
							return (new Date(window._trim(v)).Format('yyyy-MM-dd hh:mm:ss'));
						}

					}
					else{
						return '';
					}
        },
        datetimetext2:function(v){
            if (v && v !== 0){
                        if((v+'').length == 12){
                            v = v + '';
                            return (v.substring(0,4) + '-' + v.substring(4,6) + '-' + v.substring(6,8) + ' ' + v.substring(8,10) + ':' + v.substring(10,12)+ ':00');
                        }else{
                            return (new Date(window._trim(v)).Format('yyyyMMdd hh:mm:ss'));
                        }

                    }
                    else{
                        return '';
                    }
        }



};



