export function Validator(el,data){
	var data=data || {};
	this.position="top";
	if(data.vuer){
		this.vuer=data.vuer;
	}
	var _e=this.element=el;
	var ts='';//规则字符串
	if(data.success){
		this.success=data.success;
	}
	if(data.error){
		this.error=data.error;
	}
	if(data.rule!=null) {
		ts=data.rule;
	}else{
		ts=_e.getAttribute("validType");
	}
	var re= /[a-zA-Z0-9_-]+\s*(\[([#\.a-zA-Z0-9_\s-]*(,[#\.a-zA-Z0-9_\s-]*)*)\])?/ig;
	var r='';
	var rules=this.rules=[];
	while(r = re.exec(ts)) {
		var x = $.trim(r[0]);
		var rule = {};
		if (r[1] == null) {
			rule.name = x;
			rule.params = [];
		} else {
			rule.name = $.trim(x.substring(0, x.indexOf('[')));
			rule.params = r[2].split(',');
			if(rule.name ==="position"){
				this.position=rule.params[0]
			}
		}
		// 存储规则
		rules.push(rule);
	}
	if(rules.length>0){
		var _t=this;
		_e.addEventListener('blur', function() {
			_t.doValid(true);
		});
		_e.addEventListener('focus', function() {
			_t.clean();
		});
	}

}

Validator.prototype.successValid=function(){};
Validator.prototype.constructor = Validator;

// 默认级别
Validator.prototype.defaultLevel = 'info';

// 默认keyup事件激活状态
Validator.prototype.defaultKeyup = false;
/**
 * 消除提示框，并清除输入框样式
 */
Validator.prototype.success = function() {
	// if (this.element[0].shower) {
	// 	var elem = $(this.element[0].shower);
	// 	elem.removeClass('info error').addClass('valid');
	// }
	// $(this.element).removeClass('info error').addClass('valid');
	// if (this.label) {
	// 	this.label.remove();
	// 	this.label = null;
	// }
};
Validator.prototype.error = function() {

}

/**
 * 隐藏提示框，不清除输入框样式
 */
Validator.prototype.clean = function () {
	var that=this;
	if (that.label && !that.label.is(':hidden')) {
		setTimeout(function(){
			if(that.label){
				that.label.hide();
			}
		},3000)
	}
};

/**
 * 移除校验
 */
Validator.prototype.remove = function (arr) {
	if(!arr){
		this.success();
		this.rules = [];
	}else{
		var rules=this.rules;
		this.rules=rules.filter(function(item,i){
			return arr.indexOf(item.name)==-1?true:false;
		})
	}
};
Validator.prototype.defaults={rules:{
									required:{
										validator: function(value, element, params) {
											if (value == null || window._trim(value) == '')
												return false;
											return true;
										},
										message: '请将此处信息填写完整',
										level: 'info'
									}
}};
Validator.prototype.addMethod=function addMethod(name, validator, message, level) {
		Validator.prototype.defaults.rules[name] = {
			validator: validator,
			message: message,
			level: level
		};
	},

Validator.prototype.addMethod('mobile', function (value, element, params) {
		return !value?true:/^(1[1-9])\d{9}$/.test(value);
	}, function(rs, value, element, params) {
		return "手机号码不正确";
	}, 'error');
Validator.prototype.addMethod('equals', function (value, element, params) {
		if (params[0]){
			if(this.vuer.context[params[0]]){
				return this.vuer.context[params[0]]==value;
			}else{
				return params[0]==value
			}
			 
		}
		
	return true;
	}, function(rs, value, element, params) {
		return "两次输入内容不相同";
	}, 'error');


/**
 * 实际执行的验证方法 每个标签依次循环执行 async rule规则异步调用, 默认false 只是一个参数传递
 *
 * @return bo0lean 验证正确 true 错误 false
 */
Validator.prototype.doValid = function (async) {
		 var _t = this;
	 var _e = _t.element;

	if (_t.defaults == null)
		_t = {defaults:{rules:{}}};
	var ret = 1;//
	// 从存储的对象中获取规则，标签中定义的规则
	var _rs = _t.rules;
	for (var i = 0; i < _rs.length; i++) {
		// 容器中封好的规则
		var _r = _t.defaults.rules[_rs[i].name];
		// 规则中传递的参数
		var _p = _rs[i].params;
		var _v = _e.value;
		if (_r) {
			_t.curValidator = _r.validator;
			var res = _t.curValidator(_v, _e, _p);
			if (res !== true && res != null) {
				var _m;
				if (res === false) {
					_m = typeof _r.message == 'function' ? _r.message(false, _v, _e, _p) : _r.message;
				} else {
					_m = res;
				}
				//_t.tooltip(_m, _r.level);
				ret = 0;
				// 如果出现error级别校验失败，直接提示不继续校验
				if (_r.level == 'error')
					break;
			}
		}
	}
	if (ret) {
		// 验证成功清除提示框，恢复输入框样式
		this.success();
	}else{
		this.error(_m);
	}
	return ret ? true : false;
};



