export default{
	connectWebViewJavascriptBridge:function(callBack){
		if(window.WebViewJavascriptBridge){//android
			callBack(window.WebViewJavascriptBridge);
		}else{
			document.addEventListener('WebViewJavascriptBridgeReady',function(){
				callBack(window.WebViewJavascriptBridge)
			},false)
		}
	},
	init:function(){
		this.connectWebViewJavascriptBridge(function(bridge){
			bridge.init(function(message,responseCallback){
	
			})
		})
	},
	doCall:function(functionName,data,callBack){
		let _this=this;
		if(this.lastCallTime && (Date.now() - this.lastCallTime)<100){
			setTimeout(function(){
				_this.doCall(functionName,data,callBack);
			},100);
			return ;
		}
		this.lastCallTime=Date.now();
		var data=data || {};
		// if(callBack){
		// 	Object.assign(data,{callBack:callBack});
		// };
		if(window.browser.versions.ios){
			console.log("-------ios--------")
			// data.forEach(function(value,key){
			// 	if(Object.toString.call(value)==="[object Object]" || toString.call(value)==="[object Array]"){
			// 		data[key]=JSON.stringify(value);
			// 	}
			// })
			// var url="jsbridge://"+functionName+this.getParsByJson(data);
			// var iframe=document.createElement("iframe");
			// iframe.style.width="1px";
			// iframe.style.height="1px";
			// iframe.style.display="none";
			// iframe.src=url;
			// document.body.appendChild(iframe);
			// setTimeout(function(){
			// 	iframe.remove();
			// },100)

		}else if(window.browser.versions.android){

			//window.androidJS && window.androidJS[functionName] && window.androidJS[functionName](JSON.stringify(data));
			this.connectWebViewJavascriptBridge(function(bridge){

				bridge.callHandler(functionName,data,function(res){
			//var data=JSON.parse(res);
					callBack({data:res});
				})
			})

		}else{
			console.error("未获取平台信息 ，调取api失败")
		}
		// console.log("-------other--------");
	},
	getParsByJson:function(pars){
		return '';
	}
	
}


