import Vue from 'vue'
import Vuex from 'vuex'
//基本通用
import toast from './modules/toast'
import dialog from './modules/dialog'
import dict from './modules/dict'

Vue.use(Vuex)
console.log('vuex -store2 loaded')
export default new Vuex.Store({
    modules:{

        toast,
        dialog,
        dict,



    },
        strict: 'debug',
})


