import axiosUtil from '../api'
/**
 * \转化时间格式
 * 最终格式 hh:mm:ss
 */
 if(!String.prototype.endsWith){
 					String.prototype.endsWith=function(str){
 						var reg=new RegExp(str+"$");
 						return reg.test(this);
 					}
 				}

 				function accDiv(arg1,arg2){//除法
 					var t1=0,t2=0;var r1,r2;
 					try{
 						t1=arg1.toString().split('.')[1]?arg1.toString().split('.')[1].length:0;
 					}catch(e){console.log(e)}
 					try{
 						t2=arg2.toString().split('.')[1]?arg2.toString().split('.')[1].length:0;
 					}catch(e){console.log(e)}
 					r1=Number(arg1.toString().replace(".",""));
 					r2=Number(arg2.toString().replace(".",""));
 				//	console.log("r1="+r1+"r2="+r2+"t2="+t2);
 					return parseFloat(((r1/r2)*Math.pow(10,t2-t1)).toFixed(4));
 				}
 				Number.prototype.div=function(arg){//规避除法小数精确值
 					return accDiv(this,arg);
 				}
 				function accMul(arg1,arg2){//乘法
 					var t1=0,t2=0,r1,r2;
 					try{
 						t1=arg1.toString().split('.')[1]?arg1.toString().split('.')[1].length:0;
 					}catch(e){console.log(e)}
 					try{
 						t2=arg2.toString().split('.')[1]?arg2.toString().split('.')[1].length:0;
 					}catch(e){console.log(e)}
 					r1=Number(arg1.toString().replace(".",""));
 					r2=Number(arg2.toString().replace(".",""));
 					return parseFloat(((r1*r2)/Math.pow(10,t2+t1)).toFixed(4));
 				}
 				Number.prototype.mul=function(arg){//规避乘法小数精确值
 					return accMul(this,arg);
 				}
 				function accSubtr(arg1,arg2){//减法
 					var t1,t2,m,n;
 					try{
 						t1=arg1.toString().split('.')[1]?arg1.toString().split('.')[1].length:0;
 					}catch(e){console.log(e);t1=0;}
 					try{
 						t2=arg2.toString().split('.')[1]?arg2.toString().split('.')[1].length:0;
 					}catch(e){console.log(e);t2=0;}
 					m=Math.pow(10,Math.max(t1,t2));
 					n=(t1>=t2)?t1:t2;
 					return parseFloat(((arg1*m-arg2*m)/m).toFixed(n));
 				}
 				Number.prototype.subtr=function(arg){//规避减法小数精确值
 					return accSubtr(this,arg);
 				}
 				function accSum(arg1,arg2){//加法
 					var t1,t2,m,n;
 					try{
 						t1=arg1.toString().split('.')[1]?arg1.toString().split('.')[1].length:0;
 					}catch(e){console.log(e);t1=0;}
 					try{
 						t2=arg2.toString().split('.')[1]?arg2.toString().split('.')[1].length:0;
 					}catch(e){console.log(e);t2=0;}
 					m=Math.pow(10,Math.max(t1,t2));
 					n=(t1>=t2)?t1:t2;
 					return parseFloat(((arg1*m+arg2*m)/m).toFixed(n));
 				}
 				Number.prototype.sum=function(arg){//规避减法小数精确值
 					return accSum(this,arg);
 				}
	window.timeFormatter = function(value, row, index) {
		value = '' + _trim(value);
		if(value){
			if (value.length == 5){
				return '0' + value.substring(0,1) + ':' + value.substring(1,3) + ':' + value.substring(3,5);
			}else if(value.split(".")[0].length > 6){
				return (new Date(parseInt(value)).Format('hh:mm:ss'));
			}else {
				return value.substring(0,2) + ':' + value.substring(2,4) + ':' + value.substring(4,6);
			}
		}else{
			return '';
		}
	};


/**
 * \转化日期格式
 * 最终格式 yyyy-MM-dd
 */
window.dateFormatter = function (value, row, index) {
	value = '' + _trim(value);
	if (value.length > 8) {
		return new Date(parseInt(value)).Format('yyyy-MM-dd');//utc
	}
	else if (value && value != "0" && value != "19700101"){
		return value.substring(0,4) + '-' + value.substring(4,6) + '-' + value.substring(6,8);
	}
	else{
		return '-';
	}




};
/**
 * \转化日期格式
 * 最终格式 yyyy-MM-dd
 */
window.datetimeFormatter = function (value, row, index) {
//	value = _trim(value);
	if (value){
		return new Date(_trim(value)).Format('yyyy-MM-dd hh:mm:ss');
	}
//	else if (value)
//		return value.substring(0,4) + '-' + value.substring(4,6) + '-' + value.substring(6,8);
	else {
		return '';
	}
};
//根据数字获取小写金额，千分
window.toMoney = function (num) {
//	if (/^[-]?[0-9]{1,3}(,[0-9]{3})*(\.[0-9]{1,2})?$/.test(num)) {
//		return num;
//	}
//	if (!/^[-]?[0-9]+(\.[0-9]{1,2})?$/.test(num)) {
//		return '';
//	}
	if(typeof num === 'undefined' || num === '') return '';
	var result = '';
	var int = parseInt(num);
//	var float = parseInt((parseInt(parseFloat(num) * 1000) - (int * 1000)) / 10);
	var numStr = num.toString();
	var float = numStr.split('.')[1]?numStr.split('.')[1]:'00';
	var negative = false;
	if (int < 0) {
		int = 0 - int;
		negative = true;
	}
	while(int > 0) {
		var tmp = '' + (int % 1000);
		int = parseInt(int / 1000);
		var len = 3 - tmp.length;
		for (var i = 0; i < len; i++){
			tmp = '0' + tmp;
		}
		if (result === ''){
			result = tmp;
		}
		else {
			result = tmp + ',' + result;
		}
	}
	result = result.replace(/^0+/, '');
	if (negative) {
		result = '-' + result;
	}
	if(float.length === 1){
		float +='0';
	}else{
		float = float.substr(0,2)
	}
	if(result == '') result = '0';
		result += '.' + float;

	return result;
};
function _trim (v) {
	return (v || v===0) ? (v.trim ? v.trim() : v) : '';
}
window._trim=_trim;
window.dict=(function(){
	var cache = {};
	var opts = {}
		return function (ddId, ddItem){
			if (typeof ddItem == 'object') {
				return new Promise(function(resolve,reject){
					cache[ddId] = {};
					cache[ddId].data = ddItem;
					cache[ddId].index = {};
				 	resolve(ddItem);
				})

				}
				if (cache[ddId] == null ) {
					return	axiosUtil.getSysDictItems({"ddId":ddId}).then(function(data){
						// alert(ddId)
						// alert(data)
						// alert(JSON.stringify(data))
						var data=data.data;

						var data=data.data;
						if(data != null) {
							cache[ddId] = {};
							cache[ddId].data = data;
							cache[ddId].index = {};
							if (opts[ddId])
								$.each(data, function (i,v) {
									v.ddItem = v[opts[ddId].id];
									v.ddItemName = v[opts[ddId].text];
								});
						}
					}).then(function(){
						if (cache[ddId] != null) {
							if (ddItem == null) {
								//没有第二个参数时,返回数据数组
								return [].concat(cache[ddId].data);
							} else {
								//第二个参数是字符串(ddItem)时,返回ddItem对应的值。
								ddItem = $.trim(ddItem);
								if (cache[ddId].index[ddItem] == null) {
									for (var i in cache[ddId].data) {
										cache[ddId].index[cache[ddId].data[i].ddItem] = cache[ddId].data[i];
									}
								}
								var dd = cache[ddId].index[ddItem];
								if (dd == null) return null;
								else return dd.ddItemName;
							}
						}
					})
				}else if (cache[ddId] != null) {
					return new Promise(function(resolve,reject){
						if (ddItem == null) {
							//没有第二个参数时,返回数据数组
						return resolve([].concat(cache[ddId].data));
						} else {
							//第二个参数是字符串(ddItem)时,返回ddItem对应的值。
							ddItem = _trim(ddItem);
							if (cache[ddId].index[ddItem] == null) {
								for (var i in cache[ddId].data) {
									cache[ddId].index[cache[ddId].data[i].ddItem] = cache[ddId].data[i];
								}
							}
							var dd = cache[ddId].index[ddItem];
							if (dd == null)  resolve(null);
							else  resolve(dd.ddItemName);
						}
					})

			}

		}
})()
window.areaCity=(function(){
	var provObj = {};
	var cityObj = {};

	var prov_id=0;
	var city_id=0;

	var city_json=[];
	var prov_cache=[];
	var city_cache=[];
	var area_cache=[];
	var init=function init (){
		city_json.forEach(function(prov,i){
				provObj[prov.ddItem]=i;
				prov_cache.push({ddItem:prov.ddItem,ddItemName:prov.ddItemName,index:i});
			});
	}
	var cityStart=function cityStart (index){
		if(index || index==0){
			prov_id=index;
		}
		var citys=city_json[prov_id].childrens
		cityObj={};
		city_cache=[];
		citys.forEach(function(prov,i){
				cityObj[prov.ddItem]=i;
				city_cache.push({ddItem:prov.ddItem,ddItemName:prov.ddItemName,index:i});
			});
	}
	var areaStart=function areaStart (index){
		if(index || index==0){
			city_id=index;
		}
		var areas=city_json[prov_id].childrens[city_id].childrens
		area_cache=[];
		areas.forEach(function(prov,i){
				area_cache.push({ddItem:prov.ddItem,ddItemName:prov.ddItemName,index:i});
			});
	}
	return function(ddId,ddItem){
		if (!city_json.length ) {//第一次加载
					return	axiosUtil.getAreas().then(function(data){
						var data=data.data;
						//alert("area");
						var data=data.data;

						if(data != null) {
							city_json= data;
							init();
							if(ddId=="province"){
								return prov_cache;
							}else if(ddId=="city"){
								if(!ddItem){
									cityStart();
									return city_cache;
								}else{
									cityStart(provObj[ddItem]);
									return city_cache;
								}
							}else if(ddId=="area"){
								if(!ddItem){
									areaStart();
									return area_cache;
								}else{
									areaStart(ddItem);
									return area_cache;
								}
							}
							return prov_cache;
						}
					})
				}else{//数据加载成功后走缓存
						return new Promise(function(resolve,reject){
							if(ddId=="province"){
								resolve(prov_cache);
							}else if(ddId=="city"){
								if(ddItem){
									cityStart(provObj[ddItem]);
								}
								resolve(city_cache);

							}else if(ddId=="area"){
								if(ddItem){
										areaStart(cityObj[ddItem]);
								}
								resolve(area_cache);
							}
						})
				}
	};

})()


window.browser={
	versions:function(){
		var u=navigator.userAgent,app=navigator.appVersion;
		console.log("--------ua---",u)
		return {
			trident:u.indexOf('Trident')>-1,//ie内核
			presto:u.indexOf('Presto')>-1,//opera
			webKit:u.indexOf('AppleWebKit')>-1,
			gecko:u.indexOf('Gecko')>-1 && u.indexOf('KHTML') ==-1,
			mobile:!!u.match(/AppleWebKit.*Mobile.*/),
			ios:!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/),
			android:false,//u.indexOf('Android')>-1,
			iPhone:u.indexOf('iPhone')>-1,
			iPad:u.indexOf('iPad')>-1,
			webApp:u.indexOf('Safari')>-1,
			weixin:u.indexOf('MicroMessenger')>-1,
			qq:u.match(/\sQQ/i)==" qq"
		};
	}(),
	language:(navigator.browserLanguage || navigator.language).toLowerCase()
}




